.contacts {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
  }
  
  .title {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .contactList {
    list-style: none;
    padding: 0;
  }
  
  .contactItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f3f3f3;
    border-radius: 5px;
    padding: 1rem;
    margin-bottom: 1rem;
  }
  
  .addButton {
    font-size: 1rem;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .addButton:hover {
    background-color: #0056b3;
  }
  
  
  .deleteButton {
    background-color: #dc3545;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    cursor: pointer;
  }
  
  .deleteButton:hover {
    background-color: #c82333;
  }
  
  .inputFieldContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .addContactContainer {
    display: flex;
    align-items: center;
    max-width: 90vw; /* adjust as needed */
    margin: 0 auto;
  }
  
  
  .addContactInput {
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    margin-right: 16px;
    font-size: 18px;
    flex-grow: 1;
  }
  
  .addContactButton {
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    margin-right: 8px;
    font-size: 18px;
    color: white;
    background-color: #4caf50;
    cursor: pointer;
  }
  
  .addContactButton:hover {
    background-color: #388e3c;
  }
  
  .cancelButton {
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    margin-right: 8px;
    font-size: 18px;
    color: white;
    background-color: #f44336;
    cursor: pointer;
  }
  
  .cancelButton:hover {
    background-color: #d32f2f;
  }
  
  